<template>
    <div id="products">
                    <div id="subheader">
                        <div id="subheaderContent">
                            <div id="sortSelect">
                                <select @change="sortProducts(sort)" v-model="sort" name="sort" id="sortSlt" >
                                    <option value="default" disabled>Сортировка</option>
                                    <option value="name">По названию</option>
                                    <option value="priceAsc">Цена(по возрастанию)</option>
                                    <option value="priceDesc">Цена(по убыванию)</option>
                                </select>
                            </div>
                            <button @click="filterbar = true; overflow = true" id="filterBtn"><img src="@/assets/filter_alt-white-36dp.svg" alt="фильтр"></button>
                        </div>
                        
                    </div>
                    <transition name="slideFilter">
                        <filter-bar
                        @updateFilters="getFilters"
                        @categoryFilter="categoryFilter"
                        @closeFilter="filterbar = false"
                        v-show="filterbar"/>
                    </transition>
        <Product v-for="product in productsList" v-bind:key="product.id" :product_data="product"/>
    </div>
</template>
<script>
import Product from '../components/Product'
import{mapActions, mapGetters} from 'vuex'
import FilterBar from '../components/Filter'
export default {
    name: 'Products',
    components: {
        Product, FilterBar,
    },
    data(){
        return{
            sort: 'default',
            filterbar: false,
            catId: 999,
            filteredList:[]
        }
    },
    methods: {
        ...mapActions('products', ['GET_PRODUCTS','SORT_PRODUCTS']),
        sortProducts(){
            this.SORT_PRODUCTS(this.sort)
        },
        categoryFilter(data){
            this.catId = data
        },
        getFilters(data){
            this.filteredList = data
        }
    },
    computed:{
        ...mapGetters('products',['PRODUCTS']),
        productsList(){
            let prodList = []
            if(this.catId == 999 && !this.filteredList.length){
                return this.PRODUCTS
            }else{
                this.PRODUCTS.map((item)=>{
                    if(this.catId == 999){
                            for(let propName in item.props){
                                if(this.filteredList.includes(item.props[propName]) && !prodList.includes(item)){
                                    prodList.push(item)
                                }
                            }
                    }else{
                        if(item.catID == this.catId){
                            if(this.filteredList.length){
                                for(let propName in item.props){
                                    if(this.filteredList.includes(item.props[propName]) && !prodList.includes(item)){
                                        prodList.push(item)
                                    }
                            }
                            }else{
                                prodList.push(item)
                            }
                            
                        }
                    }
                     
                })
            }
            return prodList
        },
    },
    mounted(){
       // this.GET_PRODUCTS();
    }
}
</script>
