<template>
    <div class="product">
        <div class="productContent">
            <router-link  :to="'/product/'+product_data.id">
                <img class="productImg" :src="require('@/assets/'+product_data.img)" alt="">
                <h3 class="productName">{{product_data.name}}</h3>
                <div class="productProps">
                    <div v-for="(value, name) in product_data.props" :key="name" class="productProp">
                        <div class="propsName">{{name}}</div>
                        <div class="propsValue">{{value}}</div>
                    </div>
                </div>
            <div class="productPrice">- {{product_data.price}}р.</div>
            </router-link>
            
            <button @click="addToCart()" class="addToCart">в корзину</button>
        </div>
        
    </div>
</template>
<script>
import{mapActions} from 'vuex'
export default {
    name: 'Product',
    props:{product_data: Object},
    methods: {
        ...mapActions('cart',['ADD_TO_CART']),
        addToCart(){
            this.ADD_TO_CART(this.product_data);
        }
    },
    mounted(){
        this.$set(this.product_data,'qty',1);
    }
}
</script>
