<template>
    <div id="news">
        <News v-for="n in NEWS" :key="n.id" :news_data="n" />
    </div>
</template>
<script>
import News from '../components/News'
import{mapActions, mapGetters} from 'vuex'
export default {
    name: 'NewsView',
    components: {
        News,
    },
    
    methods:{
        ...mapActions('news',['GET_NEWS'])
    },
    computed:{
        ...mapGetters('news',['NEWS'])
    },
    mounted(){
        this.GET_NEWS();
    }
}
</script>
<style scoped>
#news{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    max-width: 1400px;
    margin: 0 auto;
}
</style>