import axios from 'axios'
const state = {
    news: [],
}
const mutations ={
    SET_NEWS(state, news){
        state.news = news
      },
}
const actions = {
  GET_NEWS(context){
    axios.get('https://jsonplaceholder.typicode.com/users/1/posts')
    .then((news) => {
      context.commit('SET_NEWS', news.data);
      return news;
    })
    .catch((error) => {
      return error;
    })
},
}
const getters = {
    NEWS(state){
        return state.news
      },
}
export default {
     namespaced: true,
     state,
     mutations,
     actions,
     getters
}