                            <input type="text" :value="cart_item_data.qty">
                            <button @click="addQty()">+</button>
                        </div>
                        <div class="cartItemPrice">Цена: {{cart_item_data.price}}</div>
                        <button @click="deletefromcart()" class="deleteFromCart"><img src="@/assets/delete-black-36dp.svg" alt=""></button>
                </div>
                
            </div>
            
        </div>
