export default {
    name: 'CartItem',
    props: {cart_item_data: {
        type: Object,
        default(){
            return {}
        }
    }},
    methods:{
        deletefromcart(){
            this.$emit('deleteFromCart')
        },
        addQty(){
            this.$emit('addQty')
        },
        minusQty(){
            this.$emit('minusQty')
        }
    },
    
    
}
