<template>
    <div id="oneProduct">
        <div id="productImgs">
            <img :src="require('@/assets/'+product.img)" alt="" id="currentImg">
            <!-- <div id="miniImg">
                <input type="image" v-for="(image, index) in product.img" :key="index" :src="require('@/assets/'+image)" alt="" :ref="index" class="miniImg" @click="currentImg = require('@/assets/'+image)">
            </div> -->
        </div>
        <div id="productInfo">
            <div id="productName">{{product.name}}</div>
            <div v-for="(value, prop) in product.props" :key="prop" id="productProps">
                <div class="propsName">{{prop}}:</div>
                <div class="propsValue">{{value}}</div>
            </div>
            <div id="productDescription">
                Описание:<br><br>
                {{product.description}}
                
            </div>
            <div id="cartPrice">
                <button @click="addToCart()" id="addToCart">в корзину</button>
                <div id="productPrice">- {{product.price}}р.</div>
            </div>
        </div>
    </div>
</template>
<script>
import {mapGetters, mapActions} from 'vuex'
export default {
    name: 'OneProduct',
    data(){
        return{
            
        } 
    },
    methods:{
        ...mapActions('cart', ['ADD_TO_CART']),
        addToCart(){
            this.ADD_TO_CART(this.product)
        }
    },
    computed:{
        ...mapGetters('products',['PRODUCTS']),
        product(){
            let currentProduct = {}
            this.PRODUCTS.map(item => {
                console.log(item.id == this.$route.params.id)
                if(item.id == this.$route.params.id){
                    currentProduct = item
                }
            });
            currentProduct.qty = 1;
            return currentProduct;
        }
    },
    
    
    
    
}
</script>
