const state = ()=>({
    items: [],
})

const mutations ={
    ADD_TO_CART(state, product){
      if(state.items.length){
        let isProductExist = false
        state.items.forEach(function(item){
          if(item.id === product.id){
            isProductExist = true
            item.qty++;
          }
        });
        if(!isProductExist){
          state.items.push(product)
        }
        
      }else{
        state.items.push(product)
      }
      },
    REMOVE_FROM_CART(state, index){
        state.items.splice(index, 1)
       },
    ADD_CART_ITEM_QTY(state, index){
        state.items[index].qty++
      },
    MINUS_CART_ITEM_QTY(state, index){
        state.items[index].qty--
      }
}
const actions = {
    ADD_TO_CART(context, product){
        context.commit('ADD_TO_CART', product)
      },
    REMOVE_FROM_CART(context, index){
        context.commit('REMOVE_FROM_CART', index)
      },
    ADD_CART_ITEM_QTY(context, index){
        context.commit('ADD_CART_ITEM_QTY', index)
      },
    MINUS_CART_ITEM_QTY(context, index){
        context.commit('MINUS_CART_ITEM_QTY', index)
      }
}
const getters = {
    CART(state){
        return state.items
      }
}
export default {
     namespaced: true,
     state,
     mutations,
     actions,
     getters
}