const mutations ={
    SET_PRODUCTS(state, products){
        state.products = products
      },
    SORT_PRODUCTS(state, sortType){
      switch(sortType){
        case 'name': state.products.sort(function (d1, d2) {return (d1.name.toLowerCase() > d2.name.toLowerCase()) ? 1 : -1;});
        break;
        case 'priceAsc': state.products.sort(function (d1, d2) { return (d1.price > d2.price) ? 1 : -1; });
        break;
        case 'priceDesc': state.products.sort(function (d1, d2) { return (d1.price > d2.price) ? -1 : 1; })
      }
    },
}
