const getters = {
    PRODUCTS(state){
        return state.products
      },
    CATEGORY(state){
      return state.category
    },
    ONE_PRODUCT(state, routeId){
      for(let item in state.products){
        if(routeId === item.id){
          return item
        }
      }
    }
}
export default {
     namespaced: true,
     state,
     mutations,
     actions,
     getters
}