<template>
    <div id="filterbar">
                    <div @click="closeFilter()" id="closeBtn"><img src="@/assets/close-white-18dp.svg" alt=""></div>
                    <select @change="categoryFilter()" v-model="catID" name="selectCategory" id="selectCat">
                        <option value="999" >Все категории</option>
                        <option v-for="cat in CATEGORY" :key="cat.id" :value="cat.id">{{cat.name}}</option>
                    </select>
                    <div v-for="(values, name) in getFilters" :key="name" class="filterProps">
                        <h3 class="filterPropName">{{name}}</h3>
                        <div v-for="v in values" :key="v" class="filterPropsValue">
                            <input type="checkbox" @change="updateFilters()" v-model="selectedFilters" :value="v" ><label>{{v}}</label>
                        </div>
                    
                    </div>
                
            </div>
</template>
<script>
import {mapGetters, mapActions} from 'vuex'
export default {
    name: 'FilterBar',
    data(){
        return{
            catID: 999,
            selectedFilters: []
        }
    },
    methods:{
        ...mapActions('products',['FILTER_PRODUCTS', 'CATEGORY_FILTER']),
        closeFilter(){
            this.$emit('closeFilter')
        },
        categoryFilter(){
            this.$emit('categoryFilter',this.catID);
        },
        updateFilters(){
            this.$emit('updateFilters', this.selectedFilters);
        }

    },
    computed:{
        ...mapGetters('products', ['CATEGORY', 'PRODUCTS']),
        getFilters(){
            let filters = {}
            for (let item in this.PRODUCTS){
                for(let prop in this.PRODUCTS[item].props){
                if(filters[prop]){
                    filters[prop].add(this.PRODUCTS[item].props[prop])
                }else{
                    filters[prop] = new Set();
                    filters[prop].add(this.PRODUCTS[item].props[prop])
                }
                }
            }
            return filters;
        }
    }
    
}
</script>
