     width: 100%;
    padding: 5px;
    position: absolute;
    top: 0;
    cursor: pointer;
}
#closeBtn:hover{
    background: #f7f7f7;
}
#closeBtn img{
    height: 30px;
    width: 30px;
}
#selectCat{
        margin-bottom: 20px;
        width: 90%;
        border: none;
        color: #89B837;
        font-weight: bold;
        font-size: 18px;
        background: #fff;
        padding: 10px;
        outline: none;
        text-transform: uppercase;
        cursor: pointer;
    }

    .filterProps{
        margin-bottom: 20px;
        text-align: left;
    }
    .filterPropName{
        margin-left: 10px;
        margin-bottom: 10px;
    }
    .filterPropsValue{
        padding-left: 20px;
        display: inline-flex;
