<template>
    <div id="cart">
        <cart-item 
        v-for="(item, index) in CART" :key="item.id" 
        :cart_item_data="item" 
        @deleteFromCart="deleteFromCart(index)"
        @addQty="addItemQty(index)"
        @minusQty="minusItemQty(index)"
        />
        <div v-if="CART.length" id="cartInfo">
            
            <p>Всего {{CART.length}} на сумму {{totalPrice()}}</p>
            <button @click="form = true">Заказать</button>
        </div>
        <order-form @closeForm="form = false" v-if="form"/>
    </div>
</template>
<script>
import{mapActions, mapGetters} from 'vuex'
import CartItem from '../components/CartItem'
import OrderForm from '../components/OrderForm'
export default {
    name: 'Cart',
    components:{CartItem, OrderForm},
    data(){
        return{
            form: false,
        }
    },
    computed: {
        ...mapGetters('cart',['CART']),
        
    },
    methods:{
        ...mapActions('cart',['REMOVE_FROM_CART', 'ADD_CART_ITEM_QTY', 'MINUS_CART_ITEM_QTY']),
        deleteFromCart(index){
            this.REMOVE_FROM_CART(index)
        },
        addItemQty(index){
            this.ADD_CART_ITEM_QTY(index)
        },
        minusItemQty(index){
            this.MINUS_CART_ITEM_QTY(index)
        },
        totalPrice(){
            let result = 0
            for (let index in this.CART){
                result += this.CART[index].price * this.CART[index].qty;
            }
            return result
        }
    },
}
</script>
<style scoped>
    #cart{
        max-width: 1400px;
        margin: 0 auto;

    }
    #cartInfo{
        position: relative;
        bottom: 0;
        left: 0;
        background: #000;
        color: #fff;
        max-width: 600px;
        margin: 0 auto;
        padding: 20px 0;
        font-weight: bold;
        font-size: 22px;
    }
    #cartInfo button{
        padding: 10px 20px;
        background: #89B837;
        border: none;
        color: #fff;
        outline: none;
        cursor: pointer;
        text-transform: uppercase;
        font-size: 20px;
        margin-top: 10px;
    }
    #cartInfo button:hover{
        background: #628526;
    }
    
    
    
</style>